"use client";

import Link from 'next/link';
import { useState, useEffect } from 'react';
import { navigationLinks } from '@/data/navigationLinks';
import { siteDetails } from '@/data/siteDetails';
import { Menu, X } from 'lucide-react';
import { CSSProperties } from 'react';

export default function Navbar() {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isMobile, setIsMobile] = useState(true);

  // Use useEffect for client-side detection
  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth < 768);
    };
    
    // Set initial state
    handleResize();
    
    // Add event listener
    window.addEventListener('resize', handleResize);
    
    // Clean up
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const navStyle: CSSProperties = {
    width: '100%',
    height: 'var(--nav-height)',
    backgroundColor: 'rgba(0, 0, 0, 0.97)',
    backdropFilter: 'blur(10px)',
    position: 'fixed',
    top: 0,
    left: 0,
    right: 0,
    padding: '0 1.5rem',
    borderBottom: '1px solid rgba(255, 255, 255, 0.05)',
    display: 'flex',
    alignItems: 'center',
    zIndex: 100
  };

  const containerStyle: CSSProperties = {
    maxWidth: '1280px',
    width: '100%',
    margin: '0 auto',
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center'
  };

  const logoStyle: CSSProperties = {
    display: 'flex',
    alignItems: 'center'
  };

  const logoTextStyle: CSSProperties = {
    fontSize: '1.25rem',
    fontWeight: '300',
    letterSpacing: '0.1em',
    color: 'var(--primary)',
    textTransform: 'uppercase'
  };

  const desktopNavStyle: CSSProperties = {
    display: isMobile ? 'none' : 'flex',
    alignItems: 'center',
    gap: '2.5rem'
  };

  const navLinkStyle: CSSProperties = {
    color: 'var(--foreground)',
    transition: 'color 0.3s, transform 0.3s',
    textDecoration: 'none',
    fontSize: '0.85rem',
    letterSpacing: '0.05em',
    textTransform: 'uppercase',
    fontWeight: '300',
    position: 'relative',
    padding: '0.5rem 0'
  };

  const primaryButtonStyle: CSSProperties = {
    padding: '0.5rem 1.25rem',
    backgroundColor: 'transparent',
    color: 'var(--primary)',
    border: '1px solid var(--primary)',
    fontWeight: '300',
    display: 'inline-flex',
    alignItems: 'center',
    textDecoration: 'none',
    fontSize: '0.85rem',
    letterSpacing: '0.05em',
    textTransform: 'uppercase',
    transition: 'background-color 0.3s, transform 0.3s'
  };

  const arrowStyle: CSSProperties = {
    marginLeft: '0.5rem'
  };

  const mobileButtonStyle: CSSProperties = {
    color: 'var(--foreground)',
    background: 'none',
    border: 'none',
    padding: '0.5rem',
    cursor: 'pointer',
    display: isMobile ? 'block' : 'none'
  };

  const mobileMenuStyle: CSSProperties = {
    position: 'absolute',
    top: 'var(--nav-height)',
    left: 0,
    right: 0,
    backgroundColor: 'rgba(0, 0, 0, 0.97)',
    backdropFilter: 'blur(10px)',
    borderBottom: '1px solid rgba(255, 255, 255, 0.05)',
    boxShadow: '0 10px 30px rgba(0, 0, 0, 0.3)',
    zIndex: 100
  };

  const mobileNavLinksStyle: CSSProperties = {
    display: 'flex',
    flexDirection: 'column',
    padding: '1.5rem'
  };

  const mobileLinkStyle: CSSProperties = {
    color: 'var(--foreground)',
    padding: '1rem 0',
    margin: 0,
    borderBottom: '1px solid rgba(255, 255, 255, 0.05)',
    textDecoration: 'none',
    fontSize: '0.85rem',
    letterSpacing: '0.05em',
    textTransform: 'uppercase',
    fontWeight: '300'
  };

  const mobileButtonFullStyle: CSSProperties = {
    padding: '1rem 0',
    marginTop: '1rem',
    backgroundColor: 'transparent',
    color: 'var(--primary)',
    border: '1px solid var(--primary)',
    width: '100%',
    display: 'flex',
    justifyContent: 'center',
    textAlign: 'center',
    fontSize: '0.85rem',
    letterSpacing: '0.05em',
    textTransform: 'uppercase',
    fontWeight: '300',
    textDecoration: 'none'
  };

  return (
    <nav style={navStyle}>
      <div style={containerStyle}>
        <Link href="/" style={logoStyle}>
          <span style={logoTextStyle}>{siteDetails.name}</span>
        </Link>

        {/* Desktop Navigation */}
        <div style={desktopNavStyle}>
          {navigationLinks.map((link) => (
            <Link 
              key={link.path} 
              href={link.path} 
              style={navLinkStyle}
              onMouseEnter={(e) => {
                e.currentTarget.style.color = 'var(--primary)';
                e.currentTarget.style.transform = 'translateY(-2px)';
              }}
              onMouseLeave={(e) => {
                e.currentTarget.style.color = 'var(--foreground)';
                e.currentTarget.style.transform = 'translateY(0)';
              }}
            >
              {link.name}
            </Link>
          ))}
          <Link
            href="/#contact"
            style={primaryButtonStyle}
            onMouseEnter={(e) => {
              e.currentTarget.style.backgroundColor = 'rgba(255, 255, 255, 0.05)';
              e.currentTarget.style.transform = 'translateY(-2px)';
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.backgroundColor = 'transparent';
              e.currentTarget.style.transform = 'translateY(0)';
            }}
          >
            Get Started
            <span style={arrowStyle}>→</span>
          </Link>
        </div>

        {/* Mobile Menu Button */}
        <button
          style={mobileButtonStyle}
          onClick={() => setIsMenuOpen(!isMenuOpen)}
          aria-label="Toggle menu"
        >
          {isMenuOpen ? <X size={24} /> : <Menu size={24} />}
        </button>
      </div>

      {/* Mobile Navigation */}
      {isMenuOpen && (
        <div style={mobileMenuStyle}>
          <div style={mobileNavLinksStyle}>
            {navigationLinks.map((link) => (
              <Link
                key={link.path}
                href={link.path}
                style={mobileLinkStyle}
                onClick={() => setIsMenuOpen(false)}
                onMouseEnter={(e) => e.currentTarget.style.color = 'var(--primary)'}
                onMouseLeave={(e) => e.currentTarget.style.color = 'var(--foreground)'}
              >
                {link.name}
              </Link>
            ))}
            <Link
              href="/#contact"
              style={mobileButtonFullStyle}
              onClick={() => setIsMenuOpen(false)}
            >
              Get Started
              <span style={arrowStyle}>→</span>
            </Link>
          </div>
        </div>
      )}
    </nav>
  );
}
